html{
    font-size: 62.5%;
    font-family: 'Ubuntu Mono', monospace;
}

body{
    font-size: 16px;
    background-image: url(../img/backgroundImage2.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    background-position: center;
    margin: 0;
    display: flex;
    align-items: center;
}

.subBackground{
    width: 90%;
    margin: 1.5rem auto;
    border-radius: 3rem;
    background-color: rgba(0, 0, 0, 0);
    -webkit-box-shadow: 2px 3px 31px 1px rgba(0,0,0,0.75);
    -moz-box-shadow: 2px 3px 31px 1px rgba(0,0,0,0.75);
    box-shadow: 2px 3px 31px 1px rgba(0,0,0,0.75);
    display: flex;
    align-items: flex-start;
    justify-content: center;
}

@media (min-width: 480px) {
    .subBackground{
        padding-top: 5rem;
        height: 80rem;
        margin: 5rem auto;
    } 
}

.content{
    text-align: center;
    padding: 3.5rem;
}

.logo{
    width: 16rem;
    border-radius: 50%;
    box-shadow: 0px 5px 16px 1px rgba(0,0,0,0.75);
}

.limiter{
    font-size: 3rem;
    margin-top: 1rem;
    margin-bottom: 1rem;
}

@media (min-width: 480px) {
    .limiter{
        margin-top: 2rem;
    }
}

.limiter span{
    font-size: 2.5rem;
}

.emoji{
    font-size: 2rem;
}

.insta{
    cursor: pointer;
}

.enlace{
    text-decoration: none;
}

.links{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.link{
    margin: 0.4rem;
    font-size: 1.7rem;
    color: blue;
}